import React, { useState } from 'react';
import styles from './Header.module.scss';
import { useDispatch } from 'react-redux';
import { getAnimeList } from '../../store/actions/anime';

const Header = () => {
  const [keyword, setKeyword] = useState('');
  const dispatch = useDispatch();

  const searchAnimeHandler = () => {
    if (keyword) dispatch(getAnimeList(keyword));
  };

  const searchOnEnterHandler = e => {
    if (e.key === 'Enter') {
      searchAnimeHandler();
    }
  };
  return (
    <>
      <div className={styles.header}>
        <input type='text' placeholder='Search for anime' onChange={e => setKeyword(e.target.value)} onKeyDown={searchOnEnterHandler} />
        <button onClick={e => searchAnimeHandler(e.target.value)}>Go</button>
        <br />
      </div>
      <h6 className={styles.requestInfo}>
        <span>Requesting : </span> https://api.jikan.moe/v3/search/anime?q={keyword}
      </h6>
    </>
  );
};

export default Header;
