import { searchAnime } from '../../service/http';
import { GET_ANIME, SET_ANIME, SET_LOADER, SET_ERROR } from '../types';



export const getAnimeList = (keyword) => async (dispatch) => {
    dispatch({ type: SET_LOADER, payload: true });
    const res = await searchAnime(keyword);
    if (!res) {
        dispatch({ type: SET_ERROR, payload: true })
        return;
    }
    const payload = { animeList: res?.results, keyword, lastPage: res?.last_page }
    dispatch({ type: GET_ANIME, payload });
}

export const setAnimeList = (keyword, pageNumber) => async (dispatch) => {
    dispatch({ type: SET_LOADER, payload: true });
    const res = await searchAnime(keyword, pageNumber);
    if (!res) {
        dispatch({ type: SET_ERROR, payload: true })
        return;
    }
    const payload = { animeList: res?.results, pageNumber }
    dispatch({ type: SET_ANIME, payload });
}
