import { GET_ANIME, SET_ANIME, SET_LOADER, SET_ERROR } from '../types';
import updateObject from '../../helpers/updateObject'


const INITIAL_STATE = {
    animeList: [],
    searchWord: '',
    pageNumber: 1,
    isLoading: null,
    lastPageNumber: null,
    error: false
};


const reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_ANIME:
            return updateObject(state, { animeList: action.payload?.animeList, isLoading: false, searchWord: action.payload?.keyword, pageNumber: 1, lastPageNumber: action.payload?.lastPage, error: false });
        case SET_ANIME:
            return updateObject(state, { animeList: [...state.animeList, ...action.payload?.animeList], isLoading: false, pageNumber: action.payload?.pageNumber, error: false });
        case SET_LOADER:
            return updateObject(state, { isLoading: action.payload });
        case SET_ERROR:
            return updateObject(state, { error: action.payload, isLoading: false });
        default:
            return state
    }
}


export default reducer;