import axios from 'axios';

const axiosInstance = axios.create({
    baseURL: "https://api.jikan.moe/v3"
});


/**
 * @function - search for a anime
 * @params - anime keyword
 * @params - pageNumber (optional)
 */

export const searchAnime = async (query, pageNumber = 1) => {
    try {
        if (query) {
            const { data } = await axiosInstance.get(`/search/anime?q=${query}&limit=16&page=${pageNumber}`);
            return data;
        }
    } catch (error) {
        console.log(error);
    }
}