import React from 'react';
import AnimeCard from '../AnimeCard';
import styles from './Content.module.scss';
import { useSelector, useDispatch } from 'react-redux';
import { setAnimeList } from '../../store/actions/anime';
import Spinner from '../common/Spinner';

const Content = () => {
  const animeList = useSelector(state => state.animeReducer.animeList);
  const keyword = useSelector(state => state.animeReducer.searchWord);
  const currentPage = useSelector(state => state.animeReducer.pageNumber);
  const lastPage = useSelector(state => state.animeReducer.lastPageNumber);
  const isLoading = useSelector(state => state.animeReducer.isLoading);
  const isError = useSelector(state => state.animeReducer.error);
  const dispatch = useDispatch();

  const loadMoreAnimeHandler = () => {
    const updatedPageNumber = currentPage + 1;
    dispatch(setAnimeList(keyword, updatedPageNumber));
  };

  return (
    <div className={styles.mainContainer}>
      <div className={styles.cardContainer}>
        {animeList.map(item => (
          <AnimeCard key={item.mal_id} data={item} />
        ))}
      </div>
      <div className={styles.loadMoreContainer} onClick={loadMoreAnimeHandler}>
        {!isLoading ? (
          isError ? (
            <h4 className={styles.infoText}>Error in fetching data!</h4>
          ) : animeList?.length === 0 ? (
            <h4 className={styles.infoText}>Start searching some anime!</h4>
          ) : currentPage !== lastPage ? (
            <a href='#!' className={styles.loadMoreButton}>
              Load more...
            </a>
          ) : (
            <h5 className={styles.infoText}>All data fetched!</h5>
          )
        ) : (
          <Spinner />
        )}
      </div>
    </div>
  );
};

export default Content;
